    color:#ff9900;
}
@media (max-width: 768px) {
  .menubox {
    display : none; /* Reduce padding for smaller screens */
  }
}
@media (max-width: 480px) {
  .menubox {
    display : none;  /* Further reduce padding for very small screens */
  }
}